<template>
  <div>


    <div style="padding: 10px 0">
      <el-input
          v-model="userName"
          placeholder="请输入名称"
          style="width: 200px"
          suffix-icon="el-icon-search"
      ></el-input>
      <el-input
          v-model="email"
          class="ml-5"
          placeholder="请输入邮箱"
          style="width: 200px"
          suffix-icon="el-icon-message"
      ></el-input>
      <el-input
          v-model="address"
          class="ml-5"
          placeholder="请输入地址"
          style="width: 200px"
          suffix-icon="el-icon-map-location"
      ></el-input>
      <el-button style="margin-left: 5px" type="primary" @click="load"
      >搜索</el-button
      >
      <el-button style="margin-left: 5px" type="warning" @click="reset"
      >重置</el-button
      >
    </div>

    <div>
      <el-button style="margin: 5px" type="primary" @click="handleAdd">新增<i class="el-icon-circle-plus"></i></el-button>
      <el-popconfirm
          cancel-button-text="取消"
          class="ml-5"
          confirm-button-text="确定"
          icon="el-icon-info"
          icon-color="red"
          title="确定批量删除这些数据吗"
          @confirm="delBatch"
      >
        <el-button slot="reference" style="margin: 5px" type="danger">批量删除<i class="el-icon-remove"></i></el-button>
      </el-popconfirm>
      <el-upload
        action="http://47.98.32.50:8181/user/import"  style="display: inline-block"
        :show-file-list="false"
        :headers="headersObj"
        :accept="'xlsx'"
        :on-success="handleExcelSuccess"
        >
        <el-button style="margin: 5px" type="primary" >导入<i class="el-icon-download"></i></el-button>
      </el-upload>

      <el-button style="margin: 5px" type="primary" @click="exp">导出<i class="el-icon-upload2"></i></el-button>
    </div>

    <el-table
        :data="tableData"
        border
        stripe
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="ID" prop="id" width="40"> </el-table-column>
      <el-table-column label="姓名" prop="username" width="140">
      </el-table-column>
      <el-table-column label="昵称" prop="nickname" width="120">
      </el-table-column>
      <el-table-column label="邮箱" prop="email"> </el-table-column>
      <el-table-column label="手机号" prop="phone"> </el-table-column>
      <el-table-column label="地址" prop="address"> </el-table-column>
      <el-table-column label="角色" prop="role"> </el-table-column>
      <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button type="success" @click="handleEdit(scope.row)"
          >编辑<i class="el-icon-edit-outline"></i
          ></el-button>

          <el-popconfirm
              cancel-button-text="取消"
              class="ml-5"
              confirm-button-text="确定"
              icon="el-icon-info"
              icon-color="red"
              title="确定删除吗"
              @confirm="handleDel(scope.row)"
          >
            <el-button slot="reference" type="danger"
            >删除<i class="el-icon-document-delete"></i
            ></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <div style="padding: 10px 0">
      <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :page-sizes="[2, 5, 10, 20]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handelCurrentChange"
      >
      </el-pagination>
    </div>
    <!--新增窗口-->
    <el-dialog
        :visible.sync="dialogFormVisible"
        title="用户信息"
        width="30%"
    >
      <el-form label-width="80px" size="small">
        <el-form-item label="用户名">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色">
          <el-select clearable v-model="form.role" placeholder="请选择" >
            <el-option v-for="item in roles" :key="item.name" :label="item.name" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "User",
  data(){
    return{
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      userName: "",
      email: "",
      address: "",
      dialogFormVisible: false,
      form: {},
      multipleSelection: [],
      roles:[],
    }
  },
  created() {
    //请求分页查询数据
    this.load();
  },
  computed:{
    headersObj(){
      const token = this.user.token
      return {
        token: token
      }
    }
  },
  methods: {
    load() {
      this.request
          .get("/user/page", {
            params: {
              pageNum: this.pageNum,
              pageSize: this.pageSize,
              userName: this.userName,
              email: this.email,
              address: this.address,
            },
          })
          .then((res) => {
            console.log(res);
            this.tableData = res.data.records;
            this.total = res.data.total;
          });


      this.request.get("/role").then(res=>{
        this.roles=res.data
      })

      // // 将传过来的数据转化为json格式，然后再将json格式的数据转化为对象，然后再将对象的数据赋值给tableData
      // //then()方法是用来处理异步请求的，then()方法里面的参数是一个函数，这个函数的参数是异步请求的结果
      // fetch("http://localhost:8181/user/page?pageNum="+this.pageNum+"&pageSize="+this.pageSize+"&userName="+this.userName+"&email="+this.email+"&address="+this.address).then(res=>res.json()).then(res=>{
      //   console.log(res)
      //   this.tableData=res.records
      //   this.total=res.total
      // })
    },
    handleSizeChange(pageSize) {
      console.log(pageSize);
      this.pageSize = pageSize;
      this.load();
    },
    handelCurrentChange(pageNum) {
      console.log(pageNum);
      this.pageNum = pageNum;
      this.load();
    },
    reset() {
      this.userName = "";
      this.email = "";
      this.address = "";
      this.load();
    },
    handleAdd() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    save() {
      this.request.post("/user", this.form).then((res) => {
        console.log(this.form);
        if (res.code === '200') {
          this.$message.success(res.msg)
          this.dialogFormVisible = false;
          this.load();
        } else {
          this.$message.error(res.msg)
        }
      });
    },
    handleEdit(row) {
      this.dialogFormVisible = true;
      this.form = row;
    },
    handleDel(row) {
      this.request.delete("/user/" + row.id).then((res) => {
        if (res.code === '200') {
          this.$message.success("删除成功");
          this.load();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delBatch() {
      let ids = this.multipleSelection.map((item) => item.id); //将选中的数据的id放入数组中
      console.log(ids);
      this.request.delete("/user/del/batch", {data: ids}).then((res) => {
        //delete 请求对象的data属性是用来传递参数的
        if (res.code === '200') {
          this.$message.success("批量删除成功");
        } else {
          this.$message.error(res.msg);
        }
        this.load();
      });
    },
    exp() {
      window.open("http://47.98.32.50:8181/user/export");
    },
    handleExcelSuccess(res) {
      if (res.code === '200') {
        this.$message.success("导入成功");
        this.load();
      } else {
        this.$message.error(res.msg);
      }
    }
  },
}
</script>

<style scoped>

</style>