<template>
  <div class="wrapper">
    <div class="window">
      <!--      登录-->
      <div style="margin: 20px auto;text-align: center;font-size: 25px"><b>登 录</b>
        <!--        用户名-->
      </div>
      <el-form :model="user" :rules="rules" ref="userForm">
        <el-form-item prop="username">
          <el-input size="medium" style="margin: 10px auto;" prefix-icon="el-icon-user" v-model="user.username"
                    type="username">
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <!--      密码-->
          <el-input size="medium" style="margin: 10px auto" prefix-icon="el-icon-lock" show-password
                    v-model="user.password">
          </el-input>
        </el-form-item>
        <div style="margin: 20px 40px">
          <el-button size="medium" @click="login">登录</el-button>
          <el-button size="medium" style="float: right" @click="register">注册</el-button>
        </div>
      </el-form>
    </div>
    <!--    注册窗口-->
    <el-dialog
        :visible.sync="registerVisibleHandle"
        style="width: 50%; height: auto; margin: 30px auto"
        title="注册">
      <el-form label-width="80px" :rules="rules" ref="registerForm" :model="registerUser">
        <el-form-item label="用户名" size="small" prop="username">
          <el-input v-model="registerUser.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="nickname">
          <el-input v-model="registerUser.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="registerUser.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" show-password>
          <el-input v-model="registerUser.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="password" show-password>
          <el-input v-model="registerUser.confirmPassword" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="registerUser.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="registerUser.address" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="registerCommit" type="primary">确认</el-button>
        <el-button @click="registerVisibleHandle=false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import user from "@/views/User";
import {setRouters} from "@/router";

export default {
  name: "login",
  data() {
    return {
      registerVisibleHandle: false,
      user: {
        username: "",
        password: "",
      },
      registerUser: {},
      rules: {
        username: [
          {required: true, message: "请输入用户名", trigger: 'blur'},
          {min: 3, max: 10, message: "长度在3到5个字符之间", trigger: 'blur'}
        ],
        nickname: [
          {required: true, message: "请输入昵称", trigger: 'blur'},
          {min: 3, max: 10, message: "长度在3到5个字符之间", trigger: 'blur'}
        ],
        password: [
          {required: true, message: "请输入密码", trigger: 'blur'},
        ],
        phone: [
          {required: true, message: "请输入手机号", trigger: 'blur'},
        ]
      },
    }
  },
  methods: {
    registerCommit() {
      this.$refs['registerForm'].validate((Valid) => {
            if (!Valid) {
              return false
            } else {
              if (this.registerUser.password !== this.registerUser.confirmPassword) {
                console.log(this.registerUser)
                this.$message.error("两次密码不一致")
                return false
              } else {
                this.axios.post("/user/register", this.registerUser).then(
                    res => {
                      if (res.data.code === '200') {
                        this.$message.success("注册成功")
                        this.user.username = this.registerUser.username
                        this.user.password = this.registerUser.password
                        this.registerVisibleHandle = false
                      } else {
                        this.$message.error(res.data.msg)
                      }
                    }
                )
              }
            }
          }
      )
    },
    register() {
      this.registerUser = {}
      this.registerVisibleHandle = !this.registerVisibleHandle
    },
    login() {
      //校验,只有正确输入表单才能提交
      this.$refs['userForm'].validate((valid) => {
        if (!valid) {
          return false
        } else {
          this.request.post("/user/login", this.user).then(
              res => {
                if (res.code === '200') {
                  localStorage.setItem('user', JSON.stringify(res.data))
                  setRouters()
                  this.$router.push('/')
                  //将用户信息存入localStorage,以便刷新页面时,不会丢失用户信息
                  this.$message.success("登录成功");
                } else {
                  this.$message.error(res.msg);
                }
              }
          )
        }
      })
    }
  },
}
</script>

<style scoped>
.wrapper {
  background-position: center;
  /*图片将被拉伸或缩小以覆盖整个容器，可能会有部分裁剪*/
  background-size: cover;
  /*设置背景大小*/
  height: 100%;
  /*超过的隐藏*/
  overflow: hidden;
  background-image: url('https://cdn.jsdelivr.net/gh/OrangeZSW/blog_img/202305021008781.png');
}

.window {
  background-color: white;
  /*透明度*/
  opacity: 0.6;
  width: 350px;
  height: 300px;
  margin: 200px auto;
  padding: 20px;
  /*  圆边*/
  border-radius: 5%;
}
</style>